import React from 'react';
import { useAuth } from '../../contexts/AuthContext';
import { useLanguage } from '../../contexts/LanguageContext';
import { Button } from '../ui/button';
import { LogOut, User, BookOpen, Home, Settings, Globe } from 'lucide-react';
import { Page } from '../Router';

interface HeaderProps {
  currentPage: Page;
  setCurrentPage: (page: Page) => void;
}

export const Header: React.FC<HeaderProps> = ({ currentPage, setCurrentPage }) => {
  const { user, logout } = useAuth();
  const { language, setLanguage, t } = useLanguage();

  const handleLogout = async () => {
    try {
      await logout();
      setCurrentPage('home');
    } catch (error) {
      console.error('Logout failed:', error);
    }
  };

  const toggleLanguage = () => {
    setLanguage(language === 'ru' ? 'kz' : 'ru');
  };

  return (
    <header className="sticky top-0 z-50 bg-white/95 backdrop-blur border-b border-gray-200 dark:bg-slate-900/95 dark:border-slate-800">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          <div className="flex items-center gap-2 cursor-pointer" onClick={() => setCurrentPage('home')}>
            <BookOpen className="h-6 w-6 text-blue-600" />
            <span className="text-lg font-semibold">EduPlatform</span>
          </div>

          <nav className="hidden md:flex items-center gap-2">
            <div className="rounded-full border bg-white text-gray-800 px-3 py-1 text-sm shadow-sm">
              Главная
            </div>
          </nav>

          <div className="flex items-center gap-3">
            <Button
              variant="ghost"
              size="sm"
              onClick={toggleLanguage}
              className="flex items-center gap-2"
            >
              <Globe className="h-4 w-4 text-blue-600" />
              <span className="text-sm font-medium">
                {language === 'ru' ? 'РУС' : 'ҚАЗ'}
              </span>
            </Button>

            {user ? (
              <div className="flex items-center gap-3">
                <span className="text-sm text-gray-700 dark:text-gray-300">
                  {user?.name}
                </span>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={handleLogout}
                  className="flex items-center gap-2"
                >
                  <LogOut className="h-4 w-4" />
                  <span>{t('nav.logout')}</span>
                </Button>
              </div>
            ) : (
              <Button onClick={() => setCurrentPage('auth')} variant="ghost" className="bg-black text-white hover:bg-black/90">
                Войти
              </Button>
            )}
          </div>
        </div>
      </div>
    </header>
  );
};