import React, { useState, useEffect } from 'react';
import { useLanguage } from '../../contexts/LanguageContext';
import { useAuth } from '../../contexts/AuthContext';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/card';
import { Button } from '../ui/button';
import { Badge } from '../ui/badge';
import { Progress } from '../ui/progress';
import { 
  Brain, 
  Clock, 
  Trophy, 
  Target, 
  BookOpen, 
  Users, 
  Star, 
  TrendingUp,
  Zap,
  Award,
  MessageCircle,
  BarChart3
} from 'lucide-react';
import { dataService, UserStats } from '../../utils/dataService';
import { AIAssistant } from '../ai/AIAssistant';

interface HomeProps {
  onNavigate?: (page: string) => void;
}

export const Home: React.FC<HomeProps> = ({ onNavigate }) => {
  const { t } = useLanguage();
  const { user } = useAuth();
  const [stats, setStats] = useState<UserStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [showAI, setShowAI] = useState(false);

  useEffect(() => {
    if (user) {
      loadUserStats();
    } else {
      setLoading(false);
    }
  }, [user]);

  const loadUserStats = async () => {
    try {
      const userStats = await dataService.getUserStats();
      setStats(userStats);
    } catch (error) {
      console.error('Error loading stats:', error);
      setStats({
        testsCompleted: 0,
        averageScore: 0,
        totalQuestions: 0,
        correctAnswers: 0,
        studyTime: 0,
        streak: 0,
        rank: 'Начинающий',
        achievements: [],
        recentTests: []
      });
    } finally {
      setLoading(false);
    }
  };

  // Always render marketing hero & features
  const Hero = (
    <div className="min-h-[60vh] bg-gradient-to-br from-blue-50 via-white to-indigo-50">
      <section className="container mx-auto px-4 py-16">
        <div className="max-w-6xl mx-auto text-center">
          <div className="inline-flex items-center rounded-full border border-blue-200 bg-blue-50 text-blue-700 px-3 py-1 text-xs font-medium mb-4">
            Новая версия · Поддержка Gemini/DeepSeek
          </div>
          <h1 className="text-5xl lg:text-6xl font-extrabold tracking-tight text-gray-900 mb-6 leading-tight">
            Образовательная платформа
          </h1>
          <p className="text-lg lg:text-xl text-gray-700 mb-8 max-w-3xl mx-auto">
            Изучайте, тестируйтесь, развивайтесь с помощью современных технологий ИИ
          </p>
          <div className="flex flex-wrap gap-4 justify-center">
            <Button size="lg" className="flex items-center gap-2" onClick={() => onNavigate && onNavigate('tests')}>
              <Brain className="h-5 w-5" />
              Начать тестирование
            </Button>
            <Button size="lg" variant="outline" onClick={() => setShowAI(true)} className="flex items-center gap-2">
              <MessageCircle className="h-5 w-5" />
              Попробовать ИИ помощника
            </Button>
          </div>
        </div>
      </section>
    </div>
  );

  const Features = (
    <section className="container mx-auto px-4 py-16 bg-white">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-3xl font-bold text-center mb-12">Возможности платформы</h2>
        <div className="grid gap-8 md:grid-cols-2 lg:grid-cols-3">
          <Card className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Brain className="h-6 w-6 text-blue-600" />
                {t('home.features.tests')}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-gray-600">Разнообразные тесты с таймерами и детальными результатами</p>
            </CardContent>
          </Card>
          <Card className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <BarChart3 className="h-6 w-6 text-green-600" />
                {t('home.features.progress')}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-gray-600">Подробная аналитика успеваемости и достижений</p>
            </CardContent>
          </Card>
          <Card className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Star className="h-6 w-6 text-purple-600" />
                {t('home.features.ai')}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-gray-600">Персональный ИИ помощник для объяснения тем и подготовки</p>
            </CardContent>
          </Card>
        </div>
      </div>
    </section>
  );

  return (
    <div>
      {Hero}
      {Features}
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-6xl mx-auto">
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-2">
            {user && user.name ? `Добро пожаловать, ${user.name}!` : 'Добро пожаловать!'}
          </h1>
          <p className="text-gray-600">
            Продолжайте свое обучение с нашими интерактивными инструментами
          </p>
        </div>

        <div className="grid gap-4 md:grid-cols-4 mb-8">
          <Button 
            className="h-20 flex-col gap-2" 
            variant="outline"
            onClick={() => onNavigate && onNavigate('tests')}
          >
            <Brain className="h-6 w-6" />
            <span>Новый тест</span>
          </Button>
          <Button className="h-20 flex-col gap-2" variant="outline" onClick={() => setShowAI(true)}>
            <MessageCircle className="h-6 w-6" />
            <span>ИИ помощник</span>
          </Button>
          <Button className="h-20 flex-col gap-2" variant="outline">
            <BarChart3 className="h-6 w-6" />
            <span>Статистика</span>
          </Button>
          <Button className="h-20 flex-col gap-2" variant="outline" onClick={() => onNavigate && onNavigate('diary')}>
            <BookOpen className="h-6 w-6" />
            <span>Дневник прогресса</span>
          </Button>
        </div>

        {loading ? (
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {Array.from({ length: 6 }).map((_, i) => (
              <Card key={i} className="animate-pulse">
                <CardHeader>
                  <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                </CardHeader>
                <CardContent>
                  <div className="h-8 bg-gray-200 rounded mb-2"></div>
                  <div className="h-3 bg-gray-200 rounded w-1/2"></div>
                </CardContent>
              </Card>
            ))}
          </div>
        ) : stats ? (
          <>
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3 mb-8">
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Тестов завершено</CardTitle>
                  <Trophy className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.testsCompleted}</div>
                  <p className="text-xs text-muted-foreground">
                    Общее количество пройденных тестов
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Средний балл</CardTitle>
                  <Target className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.averageScore}%</div>
                  <Progress value={stats.averageScore} className="mt-2" />
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Время обучения</CardTitle>
                  <Clock className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{Math.round(stats.studyTime / 60)} мин</div>
                  <p className="text-xs text-muted-foreground">
                    Общее время в тестах
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Серия побед</CardTitle>
                  <TrendingUp className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.streak}</div>
                  <p className="text-xs text-muted-foreground">
                    Подряд успешных тестов
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Уровень</CardTitle>
                  <Award className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.rank}</div>
                  <p className="text-xs text-muted-foreground">
                    Текущий уровень мастерства
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Достижения</CardTitle>
                  <Star className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.achievements.length}</div>
                  <p className="text-xs text-muted-foreground">
                    Получено наград
                  </p>
                </CardContent>
              </Card>
            </div>

            {stats.achievements.length > 0 && (
              <Card className="mb-8">
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Award className="h-5 w-5 text-yellow-500" />
                    Достижения
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="flex flex-wrap gap-2">
                    {stats.achievements.map((achievement, index) => (
                      <Badge key={index} variant="secondary" className="flex items-center gap-1">
                        <Star className="h-3 w-3" />
                        {achievement}
                      </Badge>
                    ))}
                  </div>
                </CardContent>
              </Card>
            )}

            {stats.recentTests.length > 0 && (
              <Card>
                <CardHeader>
                  <CardTitle>Последние тесты</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    {stats.recentTests.slice(0, 5).map((test, index) => (
                      <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                        <div>
                          <p className="font-medium">{test.subject || 'Общий тест'}</p>
                          <p className="text-sm text-gray-600">
                            {new Date(test.completedAt).toLocaleDateString('ru-RU')}
                          </p>
                        </div>
                        <div className="text-right">
                          <div className="text-lg font-bold">{test.percentage}%</div>
                          <div className="text-sm text-gray-600">
                            {test.score}/{test.total}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            )}
          </>
        ) : (
          <Card>
            <CardContent className="pt-6 text-center">
              <p className="text-gray-600">
                Начните проходить тесты, чтобы увидеть статистику!
              </p>
              <Button 
                className="mt-4"
                onClick={() => onNavigate && onNavigate('tests')}
              >
                Пройти первый тест
              </Button>
            </CardContent>
          </Card>
        )}

        {showAI && (
          <div className="mt-8 container mx-auto px-4">
            <h2 className="text-2xl font-bold mb-4">ИИ Помощник</h2>
            <AIAssistant />
            <div className="text-center mt-4">
              <Button variant="outline" onClick={() => setShowAI(false)}>
                Скрыть помощника
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
    </div>
  );
};